import React, { Component } from 'react'
import NavBar from './Components/NavBar'
import News from './Components/News'
import SportsNews from './Components/SportsNews'
import BusinessNews from './Components/BusinessNews'
import EntertainmentNews from './Components/EntertainmentNews'
import ScienceNews from './Components/ScienceNews'
import HealthNews from './Components/HealthNews'
import TechnologyNews from './Components/TechnologyNews'
import {
  HashRouter as Router,
  Routes,
  Route
} from "react-router-dom";
import LoadingBar from 'react-top-loading-bar'


// import Alert from './Components/Alert';

export class App extends Component {
 

  constructor() {
    super();
    this.state = {
      mode: 'light',
      progress: 0,
      }
    document.body.style.backgroundColor = 'white';
  }
  toggleMode = () => {
    if (this.state.mode == 'light') {
      this.setState({ mode: 'dark' });
      document.body.style.backgroundColor = '#454545';

    }
    else {
      this.setState({ mode: 'light' })
      document.body.style.backgroundColor = 'white';
    }
  }
  setProgress = (progress) => {
    this.setState({ progress: progress })
  }

  render() {
    let pageSize = 9;

    return (
      <div>
        <Router>
          <LoadingBar
            color='#f11946'
            progress={this.state.progress}

          />
          <NavBar mode={this.state.mode} toggleMode={this.toggleMode} /><div className='container'><br /><br />
            {/* <Alert alert={this.state.alert}/> */}
            
            <Routes>
              <Route exact path='/' element={<News setProgress={this.setProgress} pageSize={pageSize} mode={this.state.mode} toggleMode={this.toggleMode} key='general' category='general' />} />
              <Route exact path='/business' element={<BusinessNews setProgress={this.setProgress} pageSize={pageSize} mode={this.state.mode} toggleMode={this.toggleMode} category='business' key='business' />} />
              <Route exact path='/entertainment' element={<EntertainmentNews setProgress={this.setProgress} pageSize={pageSize} mode={this.state.mode} toggleMode={this.toggleMode} category='entertainment' key='entertainment' />} />
              {/* <Route exact path='/general' element={<News setProgress={this.setProgress}  pageSize={pageSize} mode={this.state.mode} toggleMode={this.toggleMode}  category='general' key='general'/>}/> */}
              <Route exact path='/health' element={<HealthNews setProgress={this.setProgress} pageSize={pageSize} mode={this.state.mode} toggleMode={this.toggleMode} category='health' key='health' />} />
              <Route exact path='/science' element={<ScienceNews setProgress={this.setProgress} pageSize={pageSize} mode={this.state.mode} toggleMode={this.toggleMode} category='science' key='science' />} />
              <Route exact path='/sports' element={<SportsNews setProgress={this.setProgress} pageSize={pageSize} mode={this.state.mode} toggleMode={this.toggleMode} category='sports' key='sports' />} />
              <Route exact path='/technology' element={<TechnologyNews setProgress={this.setProgress} pageSize={pageSize} mode={this.state.mode} toggleMode={this.toggleMode} category='technology' key='technology' />} />
              
            </Routes>
          </div>
        </Router>
      </div>
    )
  }
}

export default App
