import React, { Component } from 'react'


export class NewsItem extends Component {
    render() {
       let {title,description,url,imageUrl,mode}=this.props;
       
        return (
            <div>
                <div className={`card text-${mode=='dark'?'light':'dark'} border-0`} style={{ height: "340px" }}>
                    <img style={{ height: "150px" }} src={imageUrl} className="card-img-top" alt="..." />
                    <div className={`card-body bg-${mode=='dark'?'dark':'light'}`}>
                    
                        
                        <h5 style={{ height: "40px" }} className="card-title">{title}...</h5>
                        <p style={{ height: "60px" }} className="card-text">{description}...</p>
                        <center><a href={url} style={{ width: "100%" }} target='_blank' className={`btn btn-${mode=='dark'?'light':'dark'}`}>Read More</a></center>
                    </div>
                </div>
            </div>
        )
    }
}

export default NewsItem
