    constructor() {
        super();
        this.state = {
            articles: this.articles,
            loading: true,

        }
    }


    Capitalization(str) {
        return str.charAt(0).toUpperCase() + str.slice(1);
    }

    render() {
       
        let { mode, toggleMode } = this.props;

        return (
            <div>
                <h1 className={`text-${mode == 'light' ? 'dark' : 'light'}`}>{`Top Heading News`}</h1>
                <div className='container row'>
                    {this.state.articles.map((element) => {
                        return <div className='col-md-3 my-2' key={element.url}><NewsItem toggleMode={toggleMode} mode={mode} title={!element.title ? '' : element.title.slice(0, 32)} description={element.description ? element.description.slice(0, 60) : ''} url={element.url} imageUrl={!element.urlToImage ? "https://upload.wikimedia.org/wikipedia/commons/d/d1/Image_not_available.png" : element.urlToImage} /></div>
                    })}</div>
            </div>

        )
    }
